import * as actionTypes from "./home-actionType";
import axios from "axios";

export const updateComponentState = (actionType, data) => {
  return {
    type: actionType,
    payload: data,
  };
};

export const getHomeData = () => {
  let url =
    "https://spreadsheets.google.com/feeds/list/14VboNHsIozKn0do2HJfYJGraVSGZYiT2UoDDAbgp6cw/1/public/values?alt=json";

  return (dispatch) => {
    return axios
      .get(url)
      .then((res) => {
        const home_temp = res.data.feed.entry;
        const home_data = [];
        for (let a = 0; a < home_temp.length; a++) {
          let obj = {
            id: home_temp[a].gsx$id.$t,
            name: home_temp[a].gsx$name.$t,
            price: home_temp[a].gsx$price.$t,
            paid: home_temp[a].gsx$paid.$t,
            dealer: home_temp[a].gsx$dealer.$t,
            duedate: home_temp[a].gsx$duedate.$t,
          };
          home_data.push(obj);
        }
        // console.log(home_data);

        dispatch(
          updateComponentState(actionTypes.FETCH_HOME_DATA, { home_data })
        );
      })

      .catch((x) => {
        console.log(x);
      });
  };
};
