import React, { Component } from "react";
import BootstrapTable from "react-bootstrap-table-next";
import filterFactory, { textFilter } from "react-bootstrap-table2-filter";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";

import * as homeAction from "./State_Management/home-actionCreator";

// const columns = ;

class HomePage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      tableData: [],
      contextData: "contextData",
      columns: [
        {
          text: "Edit",
          isKey: true,
          formatter: this.editButton,
          headerStyle: () => {
            return { width: "70px" };
          },
        },
        {
          dataField: "id",
          text: "ID",
          headerStyle: () => {
            return { width: "200px" };
          },
        },
        {
          dataField: "name",
          text: "Product Name",
          filter: textFilter(),
        },
        {
          dataField: "price",
          text: "Product Price",
          filter: textFilter(),
        },
        {
          dataField: "paid",
          text: "Amount Paid",
          filter: textFilter(),
        },
        {
          dataField: "dealer",
          text: "Dealer Name",
          filter: textFilter(),
        },
        {
          dataField: "duedate",
          text: "Due Date",
        },
      ],
    };
  }

  editButton(cell, row) {
    return (
      <div>
        <button>Test</button>
      </div>
    );
  }

  static getDerivedStateFromProps(props, state) {
    return null;
  }

  componentDidMount() {
    this.props.callData.getHomeData("test");
  }

  render() {
    return (
      <React.Fragment>
        <div
          id="sectionHome"
          className="container"
          style={{ height: "1000px", paddingTop: "20px" }}
        >
          <BootstrapTable
            keyField="id"
            data={this.props.homeData}
            columns={this.state.columns}
            filter={filterFactory()}
          />
        </div>
      </React.Fragment>
    );
  }
}

function mapStateToProps(state) {
  console.log("mapStateToProps", state.homeReducer.homeData);
  return {
    homeData: state.homeReducer.homeData,
  };
}

function mapDispatchToProps(dispatch) {
  return {
    callData: bindActionCreators(homeAction, dispatch),
  };
}
export default connect(mapStateToProps, mapDispatchToProps)(HomePage);
