          "https://www.vpr.org/sites/vpr/files/styles/x_large/public/201902/Green-Mountain-College-name-vpr-keck-20190208.jpg",
          "https://www.hindustantimes.com/rf/image_size_960x540/HT/p2/2020/04/07/Pictures/_845f62d6-78b5-11ea-9ef9-f1be7341055a.jpg",
